@import 'src/assets/styles/constants.scss';

.survey-profile {
  text-align: center;
  &__picture {
    width: 100px;
    height: 100px;
    border: 1px solid $color-gray--lighten;
    border-radius: 50%;
    padding: 10px;
  }
  &__name, &__email {
    max-width: 300px;
    margin: 10px auto;
  }
}
